.real-estate-catalog {
  max-width: 1200px;

}

.property-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

@media screen and (max-width: 768px) {
  .property-list {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}
